	module.exports = {"data":{"post":{"html":"<p>Today we visited a new McCafe in Elmshorn, northwest of Hamburg. It's a coffee bar inside a regular McDonald's restaurant, and the idea is relatively new in Germany, although it <a href=\"http://www.indystar.com/apps/pbcs.dll/article?AID=/20050724/COLUMNISTS02/507240442/1035\" title=\"Broad Ripple McCafe making a fast-food exit\">apparently hasn't gone over well in the US</a>. Here they seem to be redoing the entire restaurant rather than creating a separate coffee area. In Elmshorn you sit on leather seats whether you are eating a Big Mac or drinking a Latte.</p>\n<p>We took some pictures <a href=\"http://www.flickr.com/photos/papascott/sets/965120/show/\">(link to slideshow)</a>.</p>\n<p><iframe align=\"center\" src=\"http://www.flickr.com/slideShow/index.gne?user_id=51035717986@N01&set_id=965120\" frameborder=\"0\" width=\"500\" scrolling=\"no\" height=\"500\"></iframe></p>\n<div><a href=\"http://www.flickr.com/photos/papascott/44094904/in/set-965120/\" title=\"mccafe01\"><img src=\"http://static.flickr.com/28/44094904_f7b4065622_s.jpg\" alt=\"mccafe01\" width=\"75\" height=\"75\" style=\"margin: 1px;\" /></a> <a href=\"http://www.flickr.com/photos/papascott/44095256/in/set-965120/\" title=\"mccafe02\"><img src=\"http://static.flickr.com/32/44095256_f99b75329e_s.jpg\" alt=\"mccafe02\" width=\"75\" height=\"75\" style=\"margin: 1px;\" /></a>\t <a href=\"http://www.flickr.com/photos/papascott/44095534/in/set-965120/\" title=\"mccafe03\"><img src=\"http://static.flickr.com/32/44095534_762d1e7b6d_s.jpg\" alt=\"mccafe03\" width=\"75\" height=\"75\" style=\"margin: 1px;\" /></a>\t <a href=\"http://www.flickr.com/photos/papascott/44095847/in/set-965120/\" title=\"mccafe04\"><img src=\"http://static.flickr.com/27/44095847_0652f8752d_s.jpg\" alt=\"mccafe04\" width=\"75\" height=\"75\" style=\"margin: 1px;\" /></a>\t <a href=\"http://www.flickr.com/photos/papascott/44096208/in/set-965120/\" title=\"mccafe05\"><img src=\"http://static.flickr.com/24/44096208_0f12bafde3_s.jpg\" alt=\"mccafe05\" width=\"75\" height=\"75\" style=\"margin: 1px;\" /></a> <a href=\"http://www.flickr.com/photos/papascott/44096488/in/set-965120/\" title=\"mccafe06\"><img src=\"http://static.flickr.com/30/44096488_176053229a_s.jpg\" alt=\"mccafe06\" width=\"75\" height=\"75\" style=\"margin: 1px;\" /></a>\t <a href=\"http://www.flickr.com/photos/papascott/44096752/in/set-965120/\" title=\"mccafe07\"><img src=\"http://static.flickr.com/26/44096752_d29a805958_s.jpg\" alt=\"mccafe07\" width=\"75\" height=\"75\" style=\"margin: 1px;\" /></a> <a href=\"http://www.flickr.com/photos/papascott/44097064/in/set-965120/\" title=\"mccafe08\"><img src=\"http://static.flickr.com/31/44097064_850343d6a5_s.jpg\" alt=\"mccafe08\" width=\"75\" height=\"75\" style=\"margin: 1px;\" /></a>\t <a href=\"http://www.flickr.com/photos/papascott/44097325/in/set-965120/\" title=\"mccafe09\"><img src=\"http://static.flickr.com/26/44097325_c2caca0eba_s.jpg\" alt=\"mccafe09\" width=\"75\" height=\"75\" style=\"margin: 1px;\" /></a>\t <a href=\"http://www.flickr.com/photos/papascott/44097580/in/set-965120/\" title=\"mccafe10\"><img src=\"http://static.flickr.com/32/44097580_c44badc170_s.jpg\" alt=\"mccafe10\" width=\"75\" height=\"75\" style=\"margin: 1px;\" /></a></div>\n<p><a href=\"http://www.creamaid.com/widget.html?id=10000000221000000850\" target=\"_blank\"><img src=\"http://www.creamaid.com/widget/1000000022\" border=\"0\" /></a><img src=\"http://www.creamaid.com/06e1443349ce15c90a92ff3a527490a5be28b970\" width=\"0\" height=\"0\" /></p>","fields":{"title":"McCafe Elmshorn","date":"17 Sep 2005","url":"https://papascott.de/2005-09-17-mccafe-elmshorn/"}}},"pathContext":{"slug":"/2005-09-17-mccafe-elmshorn/","prev":{"fields":{"title":"Unpacked and in the Rack","date":"16 Sep 2005","slug":"/2005-09-16-unpacked-and-in-the-rack/"}},"next":{"fields":{"title":"Teppich Angie","date":"17 Sep 2005","slug":"/2005-09-17-teppich-angie/"}}}}

